import React, { useState, FormEvent, useEffect } from 'react';
import GitHub, { IGitHubUser, IGitHubRateLimits } from '../../util/GitHub';
import { Container, Navbar, Nav, Row, Col, Form, FormControl, Card, Image, Button, Badge, Toast } from 'react-bootstrap';

import GitHubUserProfileItem from '../../components/GitHubProfileItem';

import logoImg from '../../assets/images/github-logo.svg';
import octocatImg from '../../assets/images/octocat.svg'

import './styles.css';

function Landing() {
  
  const defaultRateLimits = { limit: 0, remaining: 0, reset: 0 };
  
  const defaultGitHubUser: IGitHubUser = {
    name: "Follow Me (or Not)?",
    avatar_url: octocatImg
  }
  
  const [ rateLimits, setRateLimits ] = useState<IGitHubRateLimits>(defaultRateLimits);
  const [ isValidUser, setIsValidUser ] = useState(false);

  const [ username, setUsername ] = useState('');
  const [ profile, setProfile ] = useState<IGitHubUser>(defaultGitHubUser);
  const [ following, setFollowing ] = useState<IGitHubUser[]>([]);
  const [ followers, setFollowers ] = useState<IGitHubUser[]>([]);
  const [ notFollowers, setNotFollowers ] = useState<IGitHubUser[]>([]);

  const [showToast, setShowToast] = useState(false);
  const toggleShowToast = () => setShowToast(!showToast);

  function justPreventDefault(e: FormEvent) {
    e.preventDefault();
  }

  async function updateRateLimits() {
    await GitHub.getRateLimits()
      .then( (response) => {
        setRateLimits(response);
      });
  }

  async function handleIndexUser(e: FormEvent) {
    e.preventDefault();
    
    await GitHub.getUser(username)
      .then( (response) => {
        setProfile(response);
        setIsValidUser(true);
      }).catch( (error) => {
        let statusCode = error.response.status;
        
        if (statusCode === 403)
          setShowToast(true);
        
        if (statusCode === 404)
          setIsValidUser(false);
        
        setProfile(defaultGitHubUser);
      });
        
    await GitHub.getFollowing(username, 1)
      .then( (response) => {
        setFollowing(response);
      });
    
    await GitHub.getFollowers(username, 1)
      .then( (response) => {
        setFollowers(response);
      });

    updateRateLimits();
  }

  useEffect( () => {
    updateRateLimits();
  }, []);

  useEffect( () => {
    const gitUserNotFollowers = GitHub.getNotFollowers(following, followers);
    setNotFollowers(gitUserNotFollowers);
  }, [following, followers]);

  return (
    <Container fluid>
      <Navbar collapseOnSelect className="navbar-virtual-width" expand="xl" fixed="top" bg="dark" variant="dark">
        <Navbar.Brand className="nav-brand" href="#profile">
          GitHub - Follow Me (or Not)
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto" hidden={ !profile.id }>
            <Nav.Link href="#following">Following</Nav.Link>
            <Nav.Link href="#followers">Followers</Nav.Link>
            <Nav.Link href="#not-following-back">Not following back</Nav.Link>
          </Nav>
          <Navbar.Text className="ml-auto text-muted">
            <small>Remaining: { rateLimits.remaining } of { rateLimits.limit }</small>
          </Navbar.Text>
          <Form
            className="ml-auto"
            onSubmit={ justPreventDefault }
            validated={ isValidUser }
            noValidate
            inline
          >
            <FormControl
              type="text"
              className="mr-sm-2 user-form-input"
              onChange={ (e) => { setUsername(e.target.value) } }
              placeholder="Enter a GitHub username."
              autoFocus
              required
            />
            <Button
              className="user-form-button"
              variant="outline-info"
              onClick={ handleIndexUser }
              disabled={ !username }
            >Search</Button>
          </Form>
        </Navbar.Collapse>
      </Navbar>
      <Row className="content-wrapper" md={2} lg={3} xl={4}>
        <Col id="profile" className="col-anchor">
          <Toast show={ showToast } onClose={ toggleShowToast }>
            <Toast.Header>
              <img
                src={ logoImg }
                className="rounded mr-2 toast-image"
                alt=""
              />
              <strong className="mr-auto">GitHub</strong>
              <small className="text-warning">Warning!</small>
            </Toast.Header>
            <Toast.Body>
              You have reached the limit of { rateLimits.limit } requests per hour!&nbsp;
              Try again later...
            </Toast.Body>
          </Toast>
          <Card>
            <span className="text-center">
              <Image className="user-profile-image" src={ profile.avatar_url } alt={ profile.name } roundedCircle />
            </span>
            <Card.Body>
              <div className="text-center">
                <Card.Title>{ profile.name }</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">{ profile.login }</Card.Subtitle>
              </div>
              <Card.Text className="user-profile-details" hidden={ !profile.id }>
                <span>Following: { profile.following }</span>
                <span>Followers: { profile.followers }</span>
                <span>Public repos: { profile.public_repos }</span>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col id="following" className="col-anchor" hidden={ !profile.id }>
          <Card>
            <Card.Body>
              <p>
                Following:&nbsp;
                <Badge pill variant="secondary">{ profile.following }</Badge>{' '}
              </p>
              <ul className="list-unstyled list-scroll">
                { following.map( (item: IGitHubUser) => {
                  return (
                    <GitHubUserProfileItem key={ item.id } ghuser={ item } />
                    );
                  }) 
                }
              </ul>
            </Card.Body>
          </Card>
        </Col>
        <Col id="followers" className="col-anchor" hidden={ !profile.id }>
          <Card>
            <Card.Body>
              <p>
                Followers:&nbsp;
                <Badge pill variant="secondary">{ profile.followers }</Badge>{' '}
              </p>
              <ul className="list-unstyled list-scroll">
                { followers.map( (item: IGitHubUser) => {
                  return (
                    <GitHubUserProfileItem key={ item.id } ghuser={ item } />
                    );
                  }) 
                }
              </ul>
            </Card.Body>
          </Card>
        </Col>
        <Col id="not-following-back" className="col-anchor" hidden={ !profile.id }>
          <Card>
            <Card.Body>
              <p>
                Not following back:&nbsp;
                <Badge pill variant="secondary">{ notFollowers.length }</Badge>{' '}
              </p>
              <ul className="list-unstyled list-scroll">
                { notFollowers.map( (item: IGitHubUser) => {
                  return (
                    <GitHubUserProfileItem key={ item.id } ghuser={ item } />
                    );
                  }) 
                }
              </ul>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
}

export default Landing;